@media only screen and (max-width: 425px) {
  #section-welcome {
    .welcome_wrapper {
      .welcome_txt {
        font-size: 2rem;
      }

      .welcome_content_holder {
        padding: 2%;
        padding-top: 18% !important;
      }
    }

    .welcome_dy_txt_holder {
      font-size: 1rem !important;
      line-height: 2.2rem !important;
      padding-top: 3% !important;
    }
  }
}

